body {
  font: 20px Georgia serif;
  margin: 30px;
  text-align: center;
}

ol, ul {
  padding-left: 30px;
}

button {
  padding: 5px;
  background: transparent;
  border: none;
  font: 20px Georgia serif;
  margin: 5px;
}

.board-row:after {
  clear: both;
  content: "";
  display: table;
}

.status {
  margin-bottom: 10px;
}

.square {
  background: #fff;
  border: 1px solid #999;
  float: left;
  font-size: 90px;
  font-weight: bold;
  line-height: 34px;
  height: 150px;
  margin-right: -1px;
  margin-top: -1px;
  padding: 0;
  text-align: center;
  width: 150px;
}

.square:focus {
  outline: none;
}

.kbd-navigation .square:focus {
  background: #ddd;
}

.game {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
}

.game-info {
  margin-left: 20px;
}
